import React from 'react'
import {Avatar, Button, Card, Icon, message, Spin} from 'antd'
import './index.less'
import axios from "axios";
import {baseUrl} from "../../config";
import moment from "moment";
import {Link} from "react-router-dom";
import Zmage from 'react-zmage'

export default class WorkCell extends React.Component {
    constructor(props) {
        super(props);
        console.log(this.props)
    }

    type_convert = (type) => {
        switch (type) {
            case 1:
                return "";
            case 2:
                return "";
            case 3:
                return "";
            case 4:
                return "";
            case 0:
                return "";
            default:
                return "";
        }
    };

    render() {
        const p = this.props.workInfo;
        return (
            <div
                className=
                    {
                        p.stop_at < moment().unix()
                            ? 'cell-work-index cell-disable'
                            : 'cell-work-index'
                    }
                id={p.id}>
                <div className='work-cell-row'>
                    {/**/}
                    <span className='work-name'>{p.work_name}</span>
                    {/**/}
                    <span
                        className='work_money'>:{p.work_money !== 0 ? p.work_money : '/'}{p.work_tpl === 1 || p.work_tpl === 4 ? '' : ''}</span>
                </div>
                <div className='work-cell-row'>
                    <span> </span>
                    <span>:{p.work_person_number}</span>
                </div>

                <div className='work-cell-row'>
                    {/**/}
                    <Avatar size={32} shape='square' src={`${baseUrl}/${p.senter_img}`}
                            className='avatar'></Avatar>
                    {/**/}
                    <span className='name'>{p.senter_name}</span>
                    {p.stop_at ?
                        <span>:{
                            moment.unix(p.stop_at).format("HH:mm") === '23:59' && moment.unix(p.stop_at).format('MMDD') === moment().format("MMDD") ?
                                ''
                                : moment.unix(p.stop_at).format("MM-DD HH:mm")
                        }

                                </span>
                        : null}
                </div>
                <div className='work-cell-row'>
                    {/**/}
                    <span className='work_tpl'>{this.type_convert(p.work_tpl)}</span>
                    {/**/}
                    {
                        this.props.isSenter
                            ? <Link to={`/w/${p.id}`}></Link>
                            :
                            //
                            p.stop_at < moment().unix()
                                ? <Button disabled></Button>
                                :
                                <div>
                                    {/*<Button type='primary' onClick={this.working.bind(this, p.id)}></Button>*/}
                                    <span className='button-more'>
                                    {/**/}
                                        <Link to={`/w/${p.id}`}>
                                            {p.enrolled ? ',' : ''}
                                        </Link>

                                    </span>
                                </div>
                    }
                </div>
            </div>
        )
    }
}